.register {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 1) 100%),
        url("https://cdn.hipwallpaper.com/i/98/21/dUyCkp.jpg");
    background-size: cover;
    position: relative;

    .top {
        .wrapper {
            padding: 20px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .loginButton {
                background-color: red;
                border: none;
                color: white;
                border-radius: 5px;
                padding: 5px 15px;
                font-size: 16px;
                font-weight: 500;
                cursor: pointer;
                z-index: 1;
                .link{
                    color: inherit;
                    text-decoration: none;
                }
            }
        }
    }

    .container {
        width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: white;
            overflow: auto;
        .input {
            width: 300px;
                padding: 30px;
                border-radius: 5px;
                background-color: var(--main-color);
                display: flex;
                flex-direction: column;
                justify-content: space-around;

            input {
                    height: 40px;
                    margin-bottom: 10px;
                    border-radius: 5px;
                    background-color: gray;
                    color: white;
                    padding-left: 10px;
                
                    &::placeholder {
                        color: lightgray;
                    }
            }

            .registerButton {
                height: 40px;
                    border-radius: 5px;
                    background-color: red;
                    color: white;
                    border: none;
                    font-size: 18px;
                    font-weight: 500;
                    cursor: pointer;
            }
        }
    }
}